// Custom button component to choose a mission's lootlist.
function GoToMission(props) {
  // Access navigation object from inside component.
  const navigation = useNavigation();

  // Access window size.
  const { height, width } = useWindowDimensions();

  // Access theme colors.
  const { colors } = useTheme();

  return (
    <TouchableOpacity
      style={[
        styles.buttonMission,
        Platform.OS !== 'web' && {
          width: '46%',
          height: (props.boxSize)
            ? width*0.64*0.46 + (props.boxSize*15)
            : width*0.64*0.46,
        },
        Platform.OS === 'web' && {
          width: (width > 618) ? 280 : '45%',
          height: (width > 618)
            ? ((props.boxSize) ? 175 + (props.boxSize*20) : 175)
            : ((props.boxSize) ? width*0.6*0.45 + (props.boxSize*17)
              : width*0.6*0.45),
        },
        {backgroundColor: colors.backLight,
        borderColor: colors.border}
      ]}
      // Pass mission's name as a parameter to lootlist screen.
      onPress={() => navigation.navigate('Lootlist', {
        missionTitle: props.missionID,
      })}
    >
      {props.missionID &&
        <Image
          source={myScreens[props.missionID]}
          style={[
            styles.buttonImage,
            Platform.OS !== 'web' && {
              width: '95%',
              height: width*0.64*0.38,
            },
            Platform.OS === 'web' && {
              width: (width > 618) ? 270 : '94%',
              height: (width > 618) ? 150 : width*0.6*0.38,
            }
          ]}
        />
      }
      {/* Use backquotes to denote parameters in a string. */}
      {props.missionName &&
        <Text style={[
          styles.buttonText,
          Platform.OS !== 'web' && {
            fontSize: 11,
          },
          Platform.OS === 'web' && {
            fontSize: (width > 618) ? 15 : width*0.024,
          },
          {color: colors.text}
        ]}>
          {`${props.missionName}`}
          {props.subName && `:`}
        </Text>
      }
      {props.subName &&
        <Text style={[
          styles.buttonText,
          Platform.OS !== 'web' && {
            fontSize: 11,
          },
          Platform.OS === 'web' && {
            fontSize: (width > 618) ? 15 : width*0.024,
          },
          {color: colors.text}
        ]}>
          {`${props.subName}`}
        </Text>
      }
    </TouchableOpacity>
  );
}


