// Screen for displaying mission lootlist.
// Screen contents change depending on selected mission.
// Contained in stack navigator to allow going back in stack.
// 'route' allows screen to receive parameters.
function LootlistScreen({ route }) {
  const {missionTitle, carryingLoot} = route.params;
  // Access theme colors.
  const { colors } = useTheme();

  return (
    <View style={{height: '100%'}}>
      {/*<Text>missionTitle: {missionTitle}</Text>*/}
      <Lists.LootlistView
        missionName={missionTitle}
        carriedLoot={carryingLoot}
      />
    </View>
  );
}


/* **************** */
/* NOT FOUND SCREEN */
/* **************** */
// Screen for displaying an Error 404 message.
// Used in web browser in case of incorrect URL.
// Contained in stack navigator to allow going back in stack.
// 'route' allows screen to receive parameters.
function NotFoundScreen({ route }) {
  return (
    <View>
      <Text>ERROR 404!</Text>
      {route.path &&
        <Text>{`Nothing was found at "${route.path}"!`}</Text>
      }
      {!route.path &&
        <Text>Nothing was found here!</Text>
      }
    </View>
  );
}


// The root of the app.
export default function App() {
  // Configure URL paths to match navigation routes here.
  const config = {
    screens: {
      Start: {
        screens: {
          Lootlists: {
            screens: {
              TDP: 'select/thief_1',
              TMA: 'select/thief_2',
              FMs: 'select/thief_fm',
            },
          },
          Settings: 'settings',
          About: 'about',
        },
      },
      Lootlist: 'list/:missionTitle',
      NotFound: '*',
    },
  };

  // Parse incoming deep links here.
  const linking = {
    // Prefixes allow for different schemes,
    // whether the link comes from the client app or a standalone app.
    // If using universal links, add domain to the prefixes list:
    // prefixes: [Linking.createURL('/'), 'https://app.example.com'],
    prefixes: [
      Linking.createURL('/')
    ],
    config,
  };

  // Get the system's default color scheme.
  const scheme = useColorScheme();

  // Load custom fonts here.
  const [fontsLoaded] = useFonts({
    //'Batang': require('./assets/fonts/Batang.ttf'),
    //'BlueHighway': require('./assets/fonts/bluehighway.ttf'),
    'Thief': require('./assets/fonts/THIEF.ttf'),
  });

  // CurrentTheme: tracks the currently selected color theme.
  const [getCurrentTheme, setCurrentTheme] = React.useState('default');

  // DefaultGame: tracks which select screen to show upon app load.
  const [getDefaultGame, setDefaultGame] = React.useState('');

  // DefaultDiff: tracks which difficulty filters are on by default.
  const [getDefaultDiffN, setDefaultDiffN] = React.useState(true);
  const [getDefaultDiffH, setDefaultDiffH] = React.useState(true);
  const [getDefaultDiffX, setDefaultDiffX] = React.useState(true);

  // ShowList: tracks which list sections are currently selected.
  const [getShowListLoot, setShowListLoot] = React.useState(true);
  const [getShowListItem, setShowListItem] = React.useState(true);
  const [getShowListJunk, setShowListJunk] = React.useState(true);
  const [getShowListSec, setShowListSec] = React.useState(true);

  // SecSpoiler: tracks how much of secret locations to hide.
  const [getSecSpoiler, setSecSpoiler] = React.useState('none');

  // Read settings from device storage.
  const readAllSettings = async () => {
    try {
      // Use 'multiGet' to read multiple values at once.
      //const jsonValues = await AsyncStorage.multiGet([`@setting_${id}`]);
      const jsonValues = await AsyncStorage.multiGet(
        [`@setting_theme`, `@setting_game`,
        `@setting_default_normal`,
        `@setting_default_hard`, `@setting_default_expert`,
        `@setting_list_loot`, `@setting_list_item`,
        `@setting_list_junk`, `@setting_list_secret`,
        `@setting_spoiler`]
      );
      // [0] is 'setting_theme'
      if (jsonValues[0][1] !== null) {
        setCurrentTheme(jsonValues[0][1]);
      }
      // [1] is 'setting_game'
      if (jsonValues[1][1] !== null) {
        setDefaultGame(jsonValues[1][1]);
        //alert(`Loaded ${getDefaultGame}`);
      }
      // [2] is 'setting_default_normal'
      if (jsonValues[2][1] === "false") {
        setDefaultDiffN(false);
      } else {
        setDefaultDiffN(true);
      }
      // [3] is 'setting_default_hard'
      if (jsonValues[3][1] === "false") {
        setDefaultDiffH(false);
      } else {
        setDefaultDiffH(true);
      }
      // [4] is 'setting_default_expert'
      if (jsonValues[4][1] === "false") {
        setDefaultDiffX(false);
      } else {
        setDefaultDiffX(true);
      }
      // [5] is 'setting_list_loot'
      if (jsonValues[5][1] === "false") {
        setShowListLoot(false);
      } else {
        setShowListLoot(true);
      }
      // [6] is 'setting_list_item'
      if (jsonValues[6][1] === "false") {
        setShowListItem(false);
      } else {
        setShowListItem(true);
      }
      // [7] is 'setting_list_junk'
      if (jsonValues[7][1] === "false") {
        setShowListJunk(false);
      } else {
        setShowListJunk(true);
      }
      // [8] is 'setting_list_secret'
      if (jsonValues[8][1] === "false") {
        setShowListSec(false);
      } else {
        setShowListSec(true);
      }
      // [9] is 'setting_spoiler'
      if (jsonValues[9][1] !== null) {
        setSecSpoiler(jsonValues[9][1]);
      }
      //alert(`Loaded settings as ${jsonValues}`);
    } catch (e) {
      // Error: Reading the data failed.
      alert('Failed to read settings.');
    }
  };

  React.useEffect(() => {
    // When app renders, check its last saved settings.
    readAllSettings();

  }, []);

  return (
    // Wrap app root in providers to utilize context.
    <SettingContext.Provider value={
      {scheme,
      getCurrentTheme, setCurrentTheme,
      getDefaultGame, setDefaultGame,
      getDefaultDiffN, setDefaultDiffN,
      getDefaultDiffH, setDefaultDiffH,
      getDefaultDiffX, setDefaultDiffX,
      getShowListLoot, setShowListLoot,
      getShowListItem, setShowListItem,
      getShowListJunk, setShowListJunk,
      getShowListSec, setShowListSec,
      getSecSpoiler, setSecSpoiler}
    }>
      {/* Wrap app root in 'NavigationContainer' to use screen navigation. */}
      <NavigationContainer
        linking={linking}
        fallback={<Text>Loading...</Text>}
        theme={getCurrentTheme === 'default' && scheme === 'dark'
          ? MyDarkTheme
          : getCurrentTheme === 'dark'
            ? MyDarkTheme
            : MyLightTheme}
        //theme={scheme === 'dark' ? MyDarkTheme : DefaultTheme}
        //theme={MyDarkTheme}
      >
        <Stack.Navigator>
          <Stack.Screen
            name="Start"
            component={StartScreen}
            options={{headerShown: false}}
          />
          <Stack.Screen
            name="Lootlist"
            component={LootlistScreen}
            options={{headerShown: (Platform.OS === 'web') ? false : true}}
          />
          <Stack.Screen
            name="Not Found"
            component={NotFoundScreen}
            //options={{headerShown: false}}
          />
        </Stack.Navigator>
      </NavigationContainer>
    </SettingContext.Provider>
  );
}


